/* 
>>>>>>>media query for responsive NAVBAR>>>>>>>> */

@media (max-width: 54em) {
  
  

  .primary-nav {
    --underline-gap: 0.5rem;
    flex-direction: column;

    z-index: 1000;
    inset: 0 0 0 45%;
    position: fixed;
    padding: min(6rem, 20vh) 2rem;
    margin: 0;

    background-color: rgba(215, 219, 223, 0.4);
    backdrop-filter: blur(0.6rem);
  }

  .slide-out {
    transform: translateX(100%);
    transition: transform 400ms ease-in-out;
  }

  .slide-in {
    transform: translateX(0);
    transition: transform 400ms ease-in-out;
  }

  .primary-nav.underline-fnc>* {
    border: 0;
  }

  .primary-nav :hover {
    color: rgb(8, 220, 248);
  }

  .mobile-nav-hamicon {
    display: block;
    position: absolute;
    z-index: 2000;
    right: 0.9rem;
    top: 1.5rem;
    background: transparent;
    background-image: url(./icon-hamburger.svg);
    background-repeat: no-repeat;
    background-size: cover;

    width: 1.8rem;
    aspect-ratio: 1;
    border: 0;
    cursor: pointer;
  }

  .mobile-nav-close {
    display: block;
    position: absolute;
    z-index: 2000;
    right: 1.5rem;
    top: 1.5rem;
    background: transparent;
    background-image: url(./icon-close.svg);
    background-repeat: no-repeat;
    background-size: cover;

    width: 1.7rem;
    aspect-ratio: 1;
    border: 0;
    cursor: pointer;
  }
}

@media (min-width: 54em) {
  .nav>button {
    display: none;
  }
}

